.wrp{
    margin: 0 auto;
    max-width: rem(1020);
    padding: rem(10);
}

.row{
    display: flex;
    flex: 1 0 100%;
    flex-wrap: wrap;
    margin: 0 rem(-10);
    &_wrp{
        margin: 0 auto;
        max-width: rem(1020);
    }
    &_nw{
        flex-wrap: nowrap;
    }
    &_column{
        flex-direction: column;
    }
    &_col-center{
        justify-content: center;
    }
    &_col-between{
        justify-content: space-between;
    }
    &_col-middle{
        align-items: center;
    }
    &_col-bottom{
        align-items: flex-end;
    }
    &_g-5{
        margin-left: rem(-5);
        margin-right: rem(-5);
        &.row_wrp{
            margin-left: auto;
            margin-right: auto;
            padding-left: rem(5);
            padding-right: rem(5);
        }
        & > [class*="col"]{
            padding-left: rem(5);
            padding-right: rem(5);
        }
    }
    @media(min-width: 1001px){
        &_md-x-nw{
            flex-wrap: nowrap;
        }
    }
    @media(min-width: 751px){
        &_sm-x-nw{
            flex-wrap: nowrap;
        }
    }
    @media(max-width: 750px){
        &:not(.row_nw) > [class*="col"]:not([class*="xs-col"]){
            flex: 0 0 100%;
            max-width: 100%;
            margin-left: 0;
            margin-right: 0;
        }
    }
}

@mixin cols {
    flex: 0 1 auto;
    max-width: none;
    padding: rem(10);
    
    &-fill{
        flex: 1 1 100%;
        max-width: none;
        padding: rem(10);
    }
    
    $cC: 12;
    @for $i from 1 through $cC {
        &-#{$i}{
            flex: 0 0 (100 / $cC) * $i * 1%;
            max-width: (100 / $cC) * $i * 1%;
            padding: rem(10);
            @if $i == $cC {
                margin-left: 0;
                margin-right: 0;
            }
        }
        &-offset-#{$i}{
            margin-left: (100 / $cC) * $i * 1%;
        }
    }
    
    $cC: 11;
    @for $i from 1 through $cC {
        &-#{$i}{
            .row_#{$cC} > &{
                flex: 0 0 (100 / $cC) * $i * 1%;
                max-width: (100 / $cC) * $i * 1%;
                padding: rem(10);
                @if $i == $cC {
                    margin-left: 0;
                    margin-right: 0;
                }
            }
        }
        &-offset-#{$i}{
            .row_#{$cC} > &{
                margin-left: (100 / $cC) * $i * 1%;
            }
        }
    }
    
    $cC: 15;
    @for $i from 1 through $cC {
        &-#{$i}{
            .row_#{$cC} > &{
                flex: 0 0 (100 / $cC) * $i * 1%;
                max-width: (100 / $cC) * $i * 1%;
                padding: rem(10);
                @if $i == $cC {
                    margin-left: 0;
                    margin-right: 0;
                }
            }
        }
        &-offset-#{$i}{
            .row_#{$cC} > &{
                margin-left: (100 / $cC) * $i * 1%;
            }
        }
    }
    
    &_center{
        margin-left: auto;
        margin-right: auto;
    }
    &_middle{
        align-self: center;
    }
    &_right{
        margin-left: auto;
    }
    &_grow{
        flex-grow: 1;
    }
    &_first{
        order: -1;
    }
}

.col{
    @include cols;
}

@media(max-width: 1600px){
    .lg{
        &-col{
            @include cols;
        }
    }
}

@media(max-width: 1200px){
    .md{
        &-row{
            &-col-fw{
                & > [class*="col"]{
                    flex: 0 0 100%;
                    max-width: 100%;
                    margin-left: 0;
                    margin-right: 0;
                }
            }
        }
        &-col{
            @include cols;
        }
    }
}

@media(max-width: 1000px){
    .sm{
        &-row{
            &-col-fw{
                & > [class*="col"]{
                    flex: 0 0 100%;
                    max-width: 100%;
                    margin-left: 0;
                    margin-right: 0;
                }
            }
        }
        &-col{
            @include cols;
        }
    }
}

@media(max-width: 750px){
    .xs{
        &-col{
            @include cols;
        }
    }
}